import { Banner, PaginationBox, Profile, Layout, Head } from "@components";
import PostList from "./PostList";
import BannerImgSrc from "@assets/images/banner-large.png";
import ProfileImgSrc from "@assets/images/avatar.jpg";
import React from "react";
import { graphql } from "gatsby";
import styles from "@styles/Home.module.scss";

type Props = {
	pageContext: {
		current: number
		next: number | null
		prev: number | null
		total: number
		categories: { category: string, count: number, href: string }[];
		tags: { tag: string, count: number, href: string }[];
	}
	data: {
		allMarkdownRemark: {
			edges: {
				node: {
					frontmatter: {
						category: string[]
						date: string
						slug: string
						tags: string[]
						title: string
						description: string
					}
				}
			}[]
		}
	}
}

const Home: React.FC<Props> = ({
	data, pageContext: { current, next, prev, total, categories, tags }
}) => (
	<Layout categories={ categories } tags={ tags } hideNavBtnIfSmallDevice={ true }>
		<Head/>
		<section className={ styles.banner }>
			<Banner src={ BannerImgSrc }/>
			<Profile profileImg={ ProfileImgSrc } githubURL="https://github.com/jebae"/>
		</section>
		<PostList
			categories={ categories }
			tags={ tags }
			posts={ data.allMarkdownRemark.edges.map(
				({ node: { frontmatter } }) => ({ ...frontmatter })) }
		/>
		<section className={ styles.paginationBox }>
			<PaginationBox
				current={ current }
				next={ `/${next}` }
				prev={ `/${prev}` }
				total={ total }
			/>
		</section>
	</Layout>
);

export const pageQuery = graphql`
	query ($skip: Int) {
		allMarkdownRemark(
			sort: {fields: frontmatter___date, order: DESC},
			limit: 10,
			skip: $skip
		) {
			edges {
				node {
					frontmatter {
						slug
						category
						date(formatString: "YYYY년 MM월 DD일")
						tags
						title
						description
					}
				}
			}
		}
	}
`;

export default Home;
